.App {
  /* text-align: center; */
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

::-webkit-scrollbar {
    width: 7px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #34364A; 
    border-radius: 10px;
  }

  html,body{
    padding: 0;
    margin: 0;
  }

body{
    background-color: #F6F8FD;
    width: 100%;
    height: 100vh;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1{
  margin: 107px 0 0;
  text-align: center;
  color: #34364A;
  font-size: 46px;
  font-weight: 700;
}

h6{
  margin: 0 0 30px;
  text-align: center;
  color: #34364A;
  font-size: 18px;
  font-weight: 400;
}

.add-data input[type=text]{
  height: 56px;
  width: 611px;
  color: #34364A;
  background-color: #FFFFFF;
  font-size: 18px;
  font-weight: 400;
  padding: 0 32px;
  margin-right: 12px;
  border-radius: 30px;
  border: none;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.05);
}

.add-data input[type=text]:focus{
  outline: 2px solid #2447F9;
  border: none;
}

.add-data input[type=submit]{
  height: 56px;
  width: 171px;
  text-align: center;
  color: #FFFFFF;
  background-color: #2447F9;
  font-size: 18px;
  font-weight: 500;
  border-radius: 30px;
  border: none;
}

.add-data input[type=submit]:hover{
  background-color: #5671fa;
}

.list-title{
  color: #34364A;
  font-size: 27px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 20px;
}

.task-list{
  width: 850px;
  height: 45vh;
  overflow: auto;
}


.task{
  width: 100%;
  background-color: #FFFFFF;
  border-radius: 30px;
  padding: 14px 18px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  margin-bottom: 14px;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.05);
}

.task input[type=text]{
  width: 100%;
  border: none;
  font-size: 18px;
  font-weight: 400;
  color: #34364A;
  background-color: transparent;
}

.btn-group{
  margin-left: auto;
  display: flex;
}

.btn-group .btn{
  background-color: #2447F9;
  width: 42.57px;
  height: 42.57px;
  color: #FFFFFF;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-group .btn-edit{
  margin-right: 9px;
}

.btn-group .btn:hover{
  background-color: #7087FB;
}


